export class Coche {
  constructor(origin, speed = 0, label = "Coche") {
    this.origin = origin;
    this.label = label;
    this.speed = speed;
    this.angle = 0;
  }
  update(fps) {
    this.origin.x +=
      Math.cos((this.angle * Math.PI) / 180) * (this.speed / fps);
    this.origin.y +=
      Math.sin((this.angle * Math.PI) / 180) * (this.speed / fps);
  }
  draw(ctx) {
    ctx.translate(this.origin.x, this.origin.y);
    ctx.rotate((this.angle * Math.PI) / 180);
    ctx.fillRect(-50, -25, 100, 50);

    ctx.strokeStyle = "red";
    ctx.beginPath();
    ctx.arc(50, -25, 5, 0, 2 * Math.PI);
    ctx.stroke();

    ctx.beginPath();
    ctx.arc(50, 25, 5, 0, 2 * Math.PI);
    ctx.stroke();
  }
  accelerate() {
    this.speed = 100;
  }
  decelerate() {
    this.speed = 0;
  }
  turn(angle) {
    this.angle += angle;
  }
}
