export class Casita {
  constructor(origin, speed = 0, label = "Casita") {
    this.origin = origin;
    this.label = label;
    this.speed = speed;
  }
  update(fps) {
    this.origin.y += this.speed / fps;
  }
  draw(ctx) {
    ctx.translate(this.origin.x, this.origin.y);
    // https://developer.mozilla.org/en-US/docs/Web/API/CanvasRenderingContext2D/fillText
    ctx.fillText(this.label, 0, 0);
    ctx.beginPath();
    ctx.moveTo(0, 50);
    ctx.lineTo(100, 0);
    ctx.lineTo(200, 50);
    ctx.closePath();
    ctx.stroke();
  }
}
