// Get the canvas context and set the canvas html tag size
const canvas = document.getElementById("canvas");
canvas.width = window.innerWidth;
canvas.height = window.innerHeight;
const ctx = canvas.getContext("2d");

let { Casita } = require("./Casita");
let { Coche } = require("./Coche");

let coche = new Coche({ x: 100, y: 100 }, 0, "COCHE");

let actors = [coche];

/* Bucle de renderizado:
 * - Limpia el canvas
 * - Actualiza los objetos
 * - Pinta los objetos
 */
const fps = 25;
setInterval(() => {
  ctx.clearRect(0, 0, canvas.width, canvas.height);
  actors.forEach((actor) => actor.update(fps));
  actors.forEach((actor) => {
    ctx.save();
    actor.draw(ctx);
    ctx.restore();
  });
}, 1000 / fps);

// Keyboard event
window.addEventListener("keydown", (ev) => {
  if (ev.key == "ArrowUp") {
    coche.accelerate();
  }
  if (ev.key == "ArrowDown") {
    coche.decelerate();
  }
  if (ev.key == "ArrowRight") {
    coche.turn(10);
  }
  if (ev.key == "ArrowLeft") {
    coche.turn(-10);
  }
});
